import React, { useState, useRef, useEffect } from "react";
import Form from "./components/Form";
import FilterButton from "./components/FilterButton";
import Todo from "./components/Todo";
import { nanoid } from "nanoid";
import {FormControl, Button, Modal, InputGroup } from 'react-bootstrap';


function usePrevious(value) {
  const ref = useRef();
  useEffect(() => {
    ref.current = value;
  });
  return ref.current;
}

const FILTER_MAP = {
  All: () => true,
  Active: task => !task.completed,
  Completed: task => task.completed
};

const FILTER_NAMES = Object.keys(FILTER_MAP);

function App(props) {
  const [tasks, setTasks] = useState(props.tasks);
  const [filter, setFilter] = useState('All');
  const [isOn, toggleIsOn] = useModal();
  const [isAboutVisible, setAboutVisible] = useState(false);
  const [isDescriptionVisible, setDescriptionVisible] = useState(false);
  const [isCheckListVisible, setCheckListVisible] = useState(false);
  const [isLoggedin, setLoggedIn] = useState(false);
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [isFailedLogin, setFailedLogin] = useState(false);

  function toggleTaskCompleted(id) {
    const updatedTasks = tasks.map(task => {
      // if this task has the same ID as the edited task
      if (id === task.id) {
        // use object spread to make a new obkect
        // whose `completed` prop has been inverted
        return {...task, completed: !task.completed}
      }
      return task;
    });
    setTasks(updatedTasks);
  }

  function useModal(initialValue = false) {
    const [value, setValue] = React.useState(initialValue);
    const toggle = React.useCallback(() => {
      setValue(v => !v);
    }, []);
    return [value, toggle];
  }

  function deleteTask(id) {
    const remainingTasks = tasks.filter(task => id !== task.id);
    setTasks(remainingTasks);
  }


  function editTask(id, newName) {
    const editedTaskList = tasks.map(task => {
    // if this task has the same ID as the edited task
      if (id === task.id) {
        //
        return {...task, name: newName}
      }
      return task;
    });
    setTasks(editedTaskList);
  }

  const taskList = tasks
  .filter(FILTER_MAP[filter])
  .map(task => (
    <Todo
      id={task.id}
      name={task.name}
      completed={task.completed}
      key={task.id}
      toggleTaskCompleted={toggleTaskCompleted}
      deleteTask={deleteTask}
      editTask={editTask}
    />
  ));

  const filterList = FILTER_NAMES.map(name => (
    <FilterButton
      key={name}
      name={name}
      isPressed={name === filter}
      setFilter={setFilter}
    />
  ));

  function addTask(name) {
    const newTask = { id: "todo-" + nanoid(), name: name, completed: false };
    setTasks([...tasks, newTask]);
  }

  function validateLogin() {
    if(username && (username === "demouser" || username === "testuser")) {
      if(password && (password === "ThisIsForWPClass" || password === "test1234")) {
        setLoggedIn(true)
        toggleIsOn()
        setFailedLogin(false)
      } else {
        setFailedLogin(true)
      }
    } else {
      setFailedLogin(true)
    }
  }


  const tasksNoun = taskList.length !== 1 ? 'tasks' : 'task';
  const headingText = `${taskList.length} ${tasksNoun} remaining`;

  const listHeadingRef = useRef(null);
  const prevTaskLength = usePrevious(tasks.length);

  useEffect(() => {
    if (tasks.length - prevTaskLength === -1) {
      listHeadingRef.current.focus();
    }
  }, [tasks.length, prevTaskLength]);

  return (
    <div>
        <Modal show={isOn} className="todoapp stack-large" onHide={toggleIsOn}>
          <Modal.Header>
            <Modal.Title>Log In</Modal.Title>
          </Modal.Header>
           <br/>
          <Modal.Body>
            <InputGroup className="mb-3">
              <div style={{display: "flex", flexDirection: "column"}}>
                <FormControl style={{marginBottom: "10px"}}
                  onChange={event => setUsername(event.target.value)}
                  placeholder="Username"
                  aria-label="Username"
                  aria-describedby="basic-addon1"
                />
                <FormControl
                  onChange={event => setPassword(event.target.value)}
                  placeholder="Password"
                  aria-label="Password"
                  aria-describedby="basic-addon1"
                />
                {isFailedLogin ? <h4>Incorrect username/password. Please try again.</h4> : null}
              </div>
              <br/>
            </InputGroup>
          </Modal.Body>
          <Modal.Footer>
            <Button style={{marginRight: "10px"}} variant="secondary" onClick={() => {toggleIsOn()
               setFailedLogin(false)}}>
              Close
            </Button>
            <Button variant="primary" onClick={() => validateLogin()}>
              Submit
            </Button>
          </Modal.Footer>
        </Modal>
        <Modal show={isDescriptionVisible || isAboutVisible || isCheckListVisible} className="todoapp stack-large" onHide={toggleIsOn}>
          <Modal.Header>
            {isAboutVisible ? <Modal.Title><h2>About</h2></Modal.Title> : null}
            {isDescriptionVisible ? <Modal.Title><h2>Description</h2></Modal.Title> : null}
            {isCheckListVisible ? <Modal.Title><h2>Checklist</h2></Modal.Title> : null}
          </Modal.Header>
          <br/>
          <Modal.Body>
              {isAboutVisible ? <Modal.Title>
                <h3>Section 1 - About who you are</h3>
                Hello! I am Ashwin Pilgaonkar, currently pursuing Masterâ€™s in Computer Science at GSU.
                I currently work as a Graduate Research Assistant with Application Solutions at the Instructional Innovation and Technology department.
                I have 3 years of work experience as a Software Engineer, working across multiple domains - mobile as well as frontend and backend applications.

                <h3>Section 2 - Project Description / Why I chose this project</h3>
                To be honest, I had a ton of things to work on simultaneously over the last month so I decided to note them down in case I forgot any of them.
                That's when it struck me --- Hey, why not make a to-do website for my web programming project!


                <h3>Section 3 - Technologies used</h3>
                HTML, CSS, JavaScript, React.js, Firebase

                <h3>Section 4 -  What I learned in this class</h3>
                As a Software Engineer who started his journey into Web Development fairly recently in 2018, 
                I started working directly with modern frontend frameworks such as React.
                From this course, I learnt how to work with legacy web technologies such as JQuery and WordPress.
                Although these technologies aren't as widely used in the industry anymore, learning to use them
                does give one a better understanding as to what goes on under the hood in newer frameworks such as React.

              </Modal.Title> : null}
              {isDescriptionVisible ? <Modal.Title>
                This is a simple website that allows users to add tasks to a list and stores them if the user is logged in.
                It is free and open for anyone to use.
              </Modal.Title> : null}
              {isCheckListVisible ? <Modal.Title>
                Database usage: When logged in, the task list is pulled from the FireStore database
                <br/><br/>
                Ajax usage: The homepage (when logged in) uses async JavaScript (promises, since Ajax is a jquery feature) to pull the task-list from the FireStore database
                <br/><br/>
                Theme: All pages of the website use a consistent theme
                <br/><br/>
                New Library Usage: Used Firebase authentication and storage libraries to handle auth and db storage
                <br/><br/>
                Javascript Usage: Used Javascript to add dynamic functionality present on the homescreen. Also, error handling added for failed login attempts
                <br/><br/>
                Membership Area: Only logged in users can store their tasks as well as access the About / Description / Checklist pages
                <br/><br/>
                General User: Created a user with login: demouser and password: ThisIsForWPClass
                <br/><br/>
              </Modal.Title> : null}
          </Modal.Body>
          <Modal.Footer>
            <Button variant="secondary" onClick={() => {
                setDescriptionVisible(false)
                setAboutVisible(false)
                setCheckListVisible(false)
              }}>
              Close
            </Button>
          </Modal.Footer>
        </Modal>
      {
        !isOn && !isAboutVisible && !isCheckListVisible && !isDescriptionVisible ?
        <div className="todoapp stack-large">
        <div style={{display: "flex", flexDirection: "row", justifyContent: "space-around"}}>
          <Button disabled={isLoggedin} onClick={toggleIsOn} variant="link">Log in</Button>
          <Button onClick={() => {isLoggedin ? setAboutVisible(true) : toggleIsOn()} } variant="link">About</Button>
          <Button onClick={() => {isLoggedin ? setCheckListVisible(true) : toggleIsOn()} } variant="link">Checklist</Button>
          <Button onClick={() => {isLoggedin ? setDescriptionVisible(true) : toggleIsOn()} } variant="link">Description</Button>
          {/* <Button onClick={() => {setAboutVisible(true)} } variant="link">About</Button>
          <Button onClick={() => {setCheckListVisible(true)} } variant="link">Checklist</Button>
          <Button onClick={() => {setDescriptionVisible(true)} } variant="link">Description</Button> */}
        </div>
        <Form addTask={addTask} />
        <div className="filters btn-group stack-exception">
          {filterList}
        </div>
        <h2 id="list-heading" tabIndex="-1" ref={listHeadingRef}>
          {headingText}
        </h2>
        <ul
          role="list"
          className="todo-list stack-large stack-exception"
          aria-labelledby="list-heading"
        >
          {taskList}
        </ul>
        <br/><br/><span>Ashwin Pilgaonkar</span>
    </div>
        :
        null
      }
    </div>
  );
}

export default App;
